.nav {
    background: -webkit-gradient(linear, left top, right top, from(#b98c38), to(#f6fcf8));
    background: -webkit-linear-gradient(left, #b98c38, #f6fcf8);
    background: -o-linear-gradient(left, #b98c38, #f6fcf8);
    background: linear-gradient(to right, #b98c38, #f6fcf8);
}

.nav-text{
    margin: 0px 5%;
    padding: 10px 0px;
    font-size: 1.5em;
    color: #3a1d15;
    font-family: 'Courier New', Courier, monospace;
}


